<template>
    	<div class="modal-container modal-container2" ref="modalBox">
    		<div class="modal-header">
    			续排
    			<i class="el-icon-close" round @click="hideModal"></i>
    		</div>
    		<div class="modal-main">
    			<el-form ref="form" label-width="110px" class="fromBox">
		    		<div style="margin-top: 5%">
              <el-col :span="5">续排说明：</el-col><el-col :span="19">续排就是按照历史排课规律，在以下列表中选择的时间开始排课</el-col>             
            </div>
            <div class="checkList">
              <template>
                <el-checkbox-group v-model="checkedDataNew">
                  <el-checkbox v-for="(item,index) in checkedList" :key='index' :label="index" @change="changeTc($event,item,index)">
                    <ul>
                      <li><i v-if="item.type == 0">每天</i><i v-if="item.type == 1">每周{{item.day | dayNum}}</i><i v-if="item.type == 2">隔周{{item.day | dayNum}}</i>{{formatNumber(parseInt(item.day_time/3600))}}:{{formatNumber(parseInt(item.day_time/60%60))}} ( {{item.next_time | dayTime}} )&nbsp;&nbsp;&nbsp;&nbsp;已排课时：{{item.pitch_num}}</li>
                    </ul>
                  </el-checkbox>
                </el-checkbox-group>
                <!-- <el-checkbox v-else v-model="item.checked" :cur_id="item.cur_id" :cur_name="item.cur_name" @change="changeTc($event,item)">
                  <ul>
                    <ul>
                      <li>每天13:00 ( 2019-09-17 13:00 )&nbsp;&nbsp;&nbsp;&nbsp;已排课时：10</li>
                  </ul>
                  </ul>
                </el-checkbox> -->                
              </template>              
              <!-- <el-checkbox v-model="checked">
                <ul>
                  <li>钢琴</li><li>一对多</li><li>60分钟</li>
                </ul>
              </el-checkbox>
              <el-checkbox v-model="checked">
                <ul>
                  <li>钢琴</li><li>一对多</li><li>60分钟</li>
                </ul>
              </el-checkbox> -->
            </div>                                                         
		    	</el-form>
		    </div>
		    <div class="modal-footer alertFooter">
		    	<el-button type="primary" round @click="submit">保存</el-button>
		    	<el-button round @click="hideModal">取消</el-button> 
		    </div>
		</div>
</template>
<script>
  import axios from 'axios';
  import {tchedule_history} from '../../../../../../getserver/getAxios.js'
  let _this
  export default {
    name: 'rightModal',
    props: {
      that: {
        type: Object,
        default: false
      },      
      orgid: {
        type: String,
        default: false
      }               
    },
    beforeCreate: function () {
      _this = this;
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            status:'',
            selected:null,
            checkedData:{},
            checkedDataNew:[],
            checkedList:[],  
        }
    },
    created(){
      this.tchedule_history()
    },
    mounted() {
      $(".modal-container2").removeClass("modal-container3")     
    },
    filters:{
      dayNum (val){
        if(val == 0){
          return '一'
        }
        else if(val == 1){
          return '二'
        }
        else if(val == 2){
          return '三'
        }        
        else if(val == 3){
          return '四'
        }        
        else if(val == 4){
          return '五'
        }        
        else if(val == 5){
          return '六'
        }        
        else{
          return '日'
        }                
      },
      dayTime(val){
        var t = new Date(val)
        return t.getFullYear()+'-'+_this.formatNumber(t.getMonth() + 1)+'-'+_this.formatNumber(t.getDate())+' '+_this.formatNumber(t.getHours())+':'+_this.formatNumber(t.getMinutes())
      },
    },
    methods: {
        formatNumber (n) {
          n = n.toString()
          return n[1] ? n : '0' + n;
        },
        hideModal() {
          $(".modal-container2").addClass("modal-container3") 
          let _this = this
          setTimeout(function(){
            _this.$emit('hideRightModal')
          },500)    
        },      
        submit() {
         var arr = [];
         var _this = this;
         // _this.checkedData.forEach( function(element, index) {
         //  arr.push(_this.checkedData[index])
         // });
         // this.checkedDataNew = arr;
         this.$emit('changeTable',this.checkedDataNew)
        }, 
        checkedDataArry(res) {
         this.checkedDataNew = res;
        },              
        async tchedule_history () {
          let resDate = await tchedule_history(this.that.cur_id,this.that.stu_id)
          if(this.checkedDataNew.length == 0){
            this.checkedList = resDate.data.data;          
          }
          else{
            let len = this.checkedDataNew.length;
            let _this = this;
            let arr = [];
            resDate.data.data.forEach(function(element,index){
              for(var i=0;i<len;i++){
                if(_this.checkedDataNew[i].cur_id == resDate.data.data[index].cur_id){
                  resDate.data.data[index].checked = true;
                  arr[i] = _this.checkedDataNew[i];
                }
                else{
                  //resDate.data.data[index].checked = false;
                }
              }
            });
            this.checkedData = arr;
            this.checkedList = resDate.data.data;
            this.checkedDataNew = resDate.data.data;             
          }
          // console.log(this.checkedData)
          // console.log(this.checkedList)
        },         
        changeTc(event,item,index){
          if(this.checkedDataNew.indexOf(index) > -1){
            this.checkedDataNew = [];
            this.checkedDataNew.push(index);
            this.checkedData = item
            this.that.getCheckedData()            
          }
          else{
            this.checkedData = {}
            this.that.getCheckedData()            
          }      
        }                
    }
  }
</script>
