<template>
    <div class='_return'>
        <!-- 续租操作 -->
        <el-dialog
            title="续租商品"
            :visible.sync="leaseThis.renewalShow"
            v-dialogDrag>
            <section class='body__section' @scroll="scrollModalMain($event)">
                <div class='body__top'>
                    <ul class='info_list'  v-if='leaseThis.renewalShopDate!=null'>
                        <li>
                            <span>租客姓名</span>：{{leaseThis.renewalShopDate.rent_obj_name}}
                        </li>
                        <li>
                            <span>商品名称</span>：{{leaseThis.renewalShopDate.goods_name}}
                        </li>
                        <li>
                            <span>租赁数量</span>：{{leaseThis.renewalShopDate.rent_num}}
                        </li>
                        <li>
                            <span>计费方式</span>：<span v-if='leaseThis.renewalShopDate.count_type==1'>日</span>
                            <span v-if='leaseThis.renewalShopDate.count_type==2'>月</span>
                            <span v-if='leaseThis.renewalShopDate.count_type==3'>年</span>
                        </li>
                        <li>
                            <span>已交押金</span>：{{leaseThis.renewalShopDate.rent_margin}}元
                        </li>
                        <li>
                            <span>已交租金</span>：{{leaseThis.renewalShopDate.prepay}}元
                        </li>
                        <li>
                            <span>开始时间</span>：{{leaseThis.Timeinfo(leaseThis.renewalShopDate.start_time)}}
                        </li>
                        <li>
                            <span>结束时间</span>：{{leaseThis.Timeinfo(leaseThis.renewalShopDate.end_time)}}
                        </li>
                        <li class='Expire'>
                            <span>到期租金</span>：1(系统计算到期预计租金，仅供参考)
                        </li>
                        <li >
                           <span>租赁备注</span><em>：{{leaseThis.renewalShopDate.remark}}</em>
                        </li>
                    </ul>
                </div>
                <div class='body__bottom'>
                    <el-form ref="form" :model="form" label-width="20%" :label-position="labelPosition">
                        <el-form-item label="续约时间：" class='xin'>
                            <el-date-picker
                                v-model="form.RenewcontractTime"
                                type="date"
                                :editable='false'
                                placeholder="选择日期">
                            </el-date-picker>
                        </el-form-item>
                        <el-row>
                            <el-col :span='12'>
                                <el-form-item label="补交押金：">
                                        <el-input
                                            type="text"
                                            placeholder="请输入押金"
                                            v-model="form.Makeupdeposit">
                                        </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span='12'>
                                <el-form-item label="补交租金：">
                                    <el-input
                                        type="text"
                                        placeholder="请输入租金"
                                        v-model="form.Makeuprent">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="付款方式：">
                            <el-select v-model="form.payment_mode" placeholder="付款方式">
                                <el-option label= "请选择" value="0"></el-option>
                                <el-option
                                v-for="item in leaseThis.allPayDate"
                                :key="item.pay_id"
                                :label="item.pay_name"
                                :value="item.pay_id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="备 注：">
                            <el-input
                                type="textarea"
                                :rows="3"
                                maxlength="500"
                                placeholder="请输入内容"
                                v-model="form.remark">
                            </el-input>
                        </el-form-item>
                    </el-form>
                </div>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="renewalClick" size='small' >确 定</el-button>
                <el-button @click="leaseThis.renewalShow= false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getrenewal} from '../../../../../getserver/server'
export default {
  name: 'return',
  data () {
    return {
      form: {
        RenewcontractTime: '',// 续约时间
        RenewcontractTimeStap: '',// 续约时间时间戳
        Makeupdeposit:'0',// 补交押金
        Makeuprent:'0',// 补交租金
        payment_mode: '0',// 支付方式
        remark:''// 备注
      },
      labelPosition: "left"
    }
  },
  inject: ['leaseThis'],
  methods: {
    renewalClick () {
        // 续租确定
      if (this.form.RenewcontractTimeStap == '') {
          this.$message({
              message: '续约时间不能为空',
              type: 'error',
              duration: 2000,
          });
          return
      } else if (this.form.payment_mode == '0') {
          this.$message({
              message: '请选择支付方式',
              type: 'error',
              duration: 2000,
          });
          return
      } else if (this.form.Makeupdeposit == '') {
          this.form.Makeupdeposit = '0'
          this.$message({
              message: '补交押金为空，默认为0提交！',
              type: 'warning',
              duration: 2000,
          });
          
      } else if (this.form.Makeuprent == '') {
          this.form.Makeuprent = '0'
          this.$message({
              message: '补交租金为空，默认为0提交！',
              type: 'warning',
              duration: 2000,
          });
      } else if (this.form.Makeupdeposit == '.' || this.form.Makeupdeposit.length == 2 && this.form.Makeupdeposit.indexOf('.')!=-1) {
          this.form.Makeupdeposit = ''
          this.$message({
            message: '补交押金输入不规范！',
            type: 'warning',
            duration: 2000,
          });
          return
      }else if (this.form.Makeuprent == '.' || this.form.Makeuprent.length == 2 && this.form.Makeuprent.indexOf('.')!=-1) {
          this.form.Makeuprent = ''
          this.$message({
            message: '补交租金为空输入不规范！',
            type: 'warning',
            duration: 2000,
          });
          return
      }
      this.get_getrenewal()
    },
    async get_getrenewal () {
        let resDate = await getrenewal(this.leaseThis.renewalShopDate.record_id,this.form)
        if (resDate.data.status) {
          // 续租成功
            this.$message({
                message: '续租成功',
                type: 'success',
                duration: 2000,
            });
            this.leaseThis.renewalShow = false
            this.leaseThis.get_getrecords(this.leaseThis.page_num,this.leaseThis.pageSize)
        }
    }  
  },
  watch: {
    'form.Makeupdeposit'(val){
      let actualPrice = val;                    
        if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 10000000){
                this.$message.error("最大值为100万")
                actualPrice = 10000000
            }                                             
        }
        this.form.Makeupdeposit = actualPrice
    },
    'form.Makeuprent' (val) {
        let actualPrice = val;                    
        if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 10000000){
                this.$message.error("最大值为100万")
                actualPrice = 10000000
            }                                             
        }
        this.form.Makeuprent = actualPrice
    },
    'form.RenewcontractTime' (val) {
        if (val == "") {
            this.form.RenewcontractTimeStap = ''
        } else {
            let datatime = parseInt(new Date(val).getTime()) / 1000
            this.form.RenewcontractTimeStap = datatime
        }
    },
    'leaseThis.renewalShow' (val) {
        if (val == false) {
            this.form.RenewcontractTime = ''
            this.form.RenewcontractTimeStap = ''
            this.form.Makeupdeposit = '0'
            this.form.Makeuprent = '0'
            this.form.payment_mode = '0'
            this.form.remark = ''
            this.leaseThis.renewalShopDate = null
        }
    }
  }
}
</script>
