                            text-align: justify;
                        }
                        .el-form-item__label::after{
                            content: " ";
                            display: inline-block;
                            width: 100%;
                        }
                        .el-form-item__content{
                             display: flex;
                             align-items: center;
                             .el-date-editor{
                                 width: 100%;
                                 .el-input__prefix{
                                     display: none;
                                 }
                             }
                            .el-input{
                                .el-input__inner{
                                    height: vw1(40);
                                }
                            }
                        }
                    }
                    .xin{
                        .el-form-item__label::before{
                            content: "*";
                            display: inline-block;
                        }
                    }
                }
            }
        }
    }
    .el-dialog__footer{
        @include wh(100%,vw1(80));
        .el-button{
            @include wh(vw1(120),auto);
            font-size: vw1(16);
            background: #e7eaec;
            border-color: #e7eaec;
            color: #999999;
            border-radius: vw1(6);
        }
        .el-button:first-child{
            margin-right: vw1(30);
            @include bg-defaul($bg-color);
            color: #fff;
        }
    }
} 
}
