<template>
  <div class="modal-main1">
    <el-form ref="form" :model="form" :rules="rules" label-width="17%" class="fromBox">
      <div class="rightBox" @scroll="scrollModalMain($event)">
        <el-form-item prop="orgid" label="选 择 机 构">
          <el-select v-model="form.orgid" placeholder="选择机构" :disabled="true">
            <el-option :value="that.that.userLoginInfo.orgid" :key="that.that.userLoginInfo.orgid" :label="that.that.userLoginInfo.config.name"></el-option>           
          </el-select>
        </el-form-item>
        <el-form-item prop="stu_id" label="选 择 学 生" >
          <el-select v-model="form.stu_id" placeholder="请选择学生" ref="stu_id_Box" @change="changeStu"> 
            <el-option v-for="(item,index) in studentList" :value="item.stu_id" :label="item.stu_name" :key="item.stu_id"></el-option>          
          </el-select>
        </el-form-item> 
        <el-form-item prop="currm" label="课 程 名 称">
          <el-select v-model="form.currm" placeholder="选择课程" ref="currmBox" @change="changeCur">
            <el-option v-for="(item,index) in currm_list" :value="item.cur_id" :label="item.cur_name" :key="item.cur_id"></el-option>     
          </el-select>
          <div class="sinpBox" ref="sinpBox">
            <span class="s1">课时时长：<i ref="i1"></i>分钟</span>
            <span class="s1">已购课时：<i ref="i2">课时</i></span>
            <span class="s1">未排课时：<i ref="i3">课时</i></span>
            <span class="s1">已排未上：<i ref="i4">课时</i></span>
          </div>
        </el-form-item>
        <el-form-item prop="timeType" label="上 课 时 间" class="short-item">
          <el-select v-model="form.timeType" placeholder="请选择" style="margin-right: 5%" ref="hourBox" :disabled="disabled">
            <el-option :value="0" :key="0" :label="'每周'+today"></el-option>
            <el-option :value="1" :key="1" label="每天"></el-option>
            <el-option :value="2" :key="2" :label="'每两周'+today"></el-option>
          </el-select>          
          <el-select v-model="form.hours" placeholder="时" class="noRight" ref="hoursBox" :disabled="disabled">
            <el-option value="0" label="00"></el-option>
            <el-option v-for="i in 24" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh gh1" type="text">时</el-button>
          <el-select v-model="form.minutes" placeholder="分" class="minute noRight" ref="minutesBox" :disabled="disabled">
            <el-option :value="minute" label="00"></el-option>
            <el-option v-for="i in 60" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">分</el-button>          
        </el-form-item>
        <el-form-item prop="law" label="按 规 律 排" class="short-item short-item1">
          <el-select v-model="form.law" placeholder="请选择" class="noRight" ref="lawBox">
            <el-option v-for="i in 100" :value="i" :label="i" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">节课</el-button>
          <el-button class="xupai" type="text" @click="changeValue($event,selectKey)">点击续排</el-button>
        </el-form-item>               
        <el-form-item prop="c_id" label="教 室 名 称" >
          <el-select v-model="form.c_id" placeholder="请选择教室" ref="c_id_box">
            <el-option v-for="(item,index) in roomList" :value="item.room_id" :label="item.room_name" :key="item.room_id"></el-option>           
          </el-select>
        </el-form-item>
        <el-form-item prop="remarks" label="备 注" >
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="可填写备注说明"
            maxlength="500"
            v-model="form.remarks">
          </el-input>
        </el-form-item>                                                                                                
      </div>                                             
    </el-form>    
  </div>
</template>
<script>
  import axios from 'axios';
  import {add_teacher_schedule,all_students,all_list_currm,selectedRoom,get_can_arrange_cur,pl_course_info} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import {checkMaxVal100,checkText} from '../../../../../script/validate.js'
  export default {
    name: 'modal',
    props: {
      that: {
        type: Object,
        default: false
      },                  
    },
    data() {
        return {
            status:'',
            studentList: [],
            currm_list:[],
            roomList:[],
            checkedData:{},
            minute:0,
            hour:0,
            today:'',
            tds:null,
            disabled:false,
            c_type:1,//一对一
            form: {
              hours:7,
              minutes:30,
              timeType:0,              
              orgid:this.that.that.userLoginInfo.orgid,
              stu_id:null,
              currm:null,
              law:null,
              c_id:null,
              remarks:'',
            },
            rules: {
              orgid: [
              { required: true,},
              ],
              stu_id: [
              { required: true}
              ],
              currm: [
              { required: true}
              ],
              c_id: [
              { required: true}
              ],
              timeType: [
              { required: true}
              ],
              law: [
              { required: true}
              ],                                                                                                           
            },
            selectKey: 1,
            Imageport          
        }
    },
    mounted() {
       this.tds = this.that.clickId          
    },
    created(){
      this.all_students()
      this.selectedRoom()
    },
    watch: {
      checkedData:function(){
        if(JSON.stringify(this.checkedData) == '{}'){
          this.tds = this.that.clickId
          this.form.hours = 7
          this.form.minutes = 30
          this.disabled = false
        }
        else{
          this.tds = this.checkedData.day
          this.form.hours = parseInt(this.checkedData.day_time/3600)
          this.form.minutes = parseInt(this.checkedData.day_time/60%60)
          console.log(this.$refs.hourBox)
          this.disabled = true        
        }
      },
      tds:function(){
        if(this.tds == 7){
          this.today = '日'
        }
        else if(this.tds == 1){
          this.today = '一'
        }
        else if(this.tds == 2){
          this.today = '二'
        }      
        else if(this.tds == 3){
          this.today = '三'
        }      
        else if(this.tds == 4){
          this.today = '四'
        }      
        else if(this.tds == 5){
          this.today = '五'
        }      
        else if(this.tds == 6){
          this.today = '六'
        }         
      },
    },
    components:{
    	
    },
    methods: {
      appendzero:function(obj)
      {
        if(obj<10) return "0" +""+ obj;
        else return obj;
      },
      changeStu(){
        this.that.stu_id = this.form.stu_id
        this.form.currm = null
        this.get_can_arrange_cur()
      },
      changeCur(){
        this.that.cur_id = this.form.currm
        this.pl_course_info ()
        this.$refs.sinpBox.style.display = "block"
      },
      async all_students () {  
        let resDate = await all_students(this.that.that.userLoginInfo.orgid)
        if (resDate.data.status) {
          this.studentList = resDate.data.data        
        }
      },
      async get_can_arrange_cur () {  
        let resDate = await get_can_arrange_cur(this.form.stu_id)
        if (resDate.data.status) {
          this.currm_list = resDate.data.data        
        }
      },
      async selectedRoom () {  
        let resDate = await selectedRoom(this.that.that.userLoginInfo.orgid)
        if (resDate.data.status) {
          this.roomList = resDate.data.data        
        }
      },
      async pl_course_info () {  
        let resDate = await pl_course_info(this.form.currm,this.form.stu_id)
        if (resDate.data.status) {
          this.$refs.i1.innerText=resDate.data.data[0].ctime
          this.$refs.i2.innerText=resDate.data.data[0].class_hour
          this.$refs.i3.innerText=resDate.data.data[0].surplus_hour       
        }
      },                                          
      async add_teacher_schedule() {
        if(!this.form.stu_id){
          this.$message.error('请选择学生');
          this.$refs.stu_id_Box.$el.children[0].children[0].click();
          return;            
        }
        else if(!this.form.currm){
          this.$message.error('你选择课程');
          this.$refs.currmBox.$el.children[0].children[0].click();
          return;            
        }
        else if(!this.form.law){
          this.$message.error('请选择排课规律');
          this.$refs.lawBox.$el.children[0].children[0].click();
          return;            
        }          
        else if(!this.form.c_id){
          this.$message.error('请选择教室');
          this.$refs.c_id_box.$el.children[0].children[0].click();
          return;            
        };
        let day =  this.that.clickId;
        let toDays = new Date(this.that.toDays + ' ' + this.appendzero(this.form.hours) + ':' + this.appendzero(this.form.minutes) + ':' + '00').getTime()/1000 - new Date(this.that.toDays + ' 00:00:00').getTime()/1000
        let today =  new Date(this.that.toDays + ' 00:00:00').getTime()/1000                                                   
        let resDate = await add_teacher_schedule(this.form.stu_id,this.form.currm,this.that.that.teacherId,this.form.timeType,this.form.law,this.form.c_id,this.form.remarks,day,toDays,today,this.c_type);
        let _this = this;
        if (resDate.data.status) {
          this.$message({
            type: 'success',
            message: resDate.data.sinfo
          });          
          this.that.$emit('submit');
        }
      },       
      changeValue(event,b){
        if(this.form.stu_id && this.form.currm){
          if(event && this.selectKey == '1'){
            this.selectKey = 2;
            this.showRight()
            this.show1 = true;
          }
          else{
            this.selectKey = 1;
            this.that.submitRight()
            this.show1 = false;            
          }          
        }
        else{
          if(this.form.stu_id){
            this.$message.error('请选择课程');
            this.$refs.currmBox.$el.children[0].children[0].click()
          }
          else{
            this.$message.error('请选择学生');
            this.$refs.stu_id_Box.$el.children[0].children[0].click()
          }
        }
    },        
    showRight(){
      this.that.showRight()
    },      	          
    }
  }
</script>
