<template>
  <div class="modal-main1">
    <el-form ref="form" :model="form" :rules="rules" label-width="17%" class="fromBox">
      <div class="rightBox" @scroll="scrollModalMain($event)">
        <el-form-item prop="name" label="选 择 机 构" >
          <el-select v-model="form.orgid" placeholder="选择机构" ref="mySelect" :disabled="true"> 
            <el-option :value="that.that.userLoginInfo.orgid" :key="that.that.userLoginInfo.orgid" :label="that.that.userLoginInfo.config.name"></el-option>          
          </el-select>
        </el-form-item>
        <el-form-item prop="" label="显 示 课 程" style="display: none">
          <!-- <el-checkbox v-model="form.cur_state" @change="changeAllClass" style="display: none">显示所有课程</el-checkbox> -->          
          <el-input ref="allClaaInput" v-model="form.currmName"></el-input>
        </el-form-item>        
        <el-form-item prop="currm" label="课 程 名 称" >
          <el-select v-model="form.currm" placeholder="选择课程">
            <el-option v-for="(item,index) in currmList" :value="item.cur_id" :label="item.cur_name" :key="item.cur_id"></el-option>         
          </el-select>
        </el-form-item>
        <el-form-item prop="stu_id" label="添 加 学 生" class="short-item short-item2">
          <el-input v-model="form.student" placeholder="请添加学生" readonly @focus="changeValue1($event,selectKey)">
          </el-input>
          <el-button class="xupai" type="text" @click="changeValue1($event,selectKey)">添加学生</el-button>
        </el-form-item>        
        <el-form-item prop="hour" label="上 课 时 间" class="short-item">
          <el-select v-model="form.timeType" placeholder="请选择" style="margin-right: 5%">
            <el-option :value="0" :key="0" :label="'每周'+today"></el-option>
            <el-option :value="1" :key="1" label="每天"></el-option>
            <el-option :value="2" :key="2" :label="'每两周'+today"></el-option>
          </el-select>          
          <el-select v-model="form.hour" placeholder="时" class="noRight">
            <el-option value="0" label="00"></el-option>
            <el-option v-for="i in 24" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh gh1" type="text">时</el-button>
          <el-select v-model="form.minute" placeholder="分" class="minute noRight">
            <el-option :value="minute" label="00"></el-option>
            <el-option v-for="i in 60" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">分</el-button>          
        </el-form-item>
        <el-form-item prop="law" label="按 规 律 排" class="short-item short-item1">
          <el-select v-model="form.law" placeholder="请选择" class="noRight" ref="lawBox">
            <el-option v-for="i in 100" :value="i" :label="i" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">节课</el-button> 
          <el-button class="xupai" type="text" @click="changeValue($event,selectKey)">点击续排</el-button>
        </el-form-item>               
        <el-form-item prop="c_id" label="教 室 名 称" >
          <el-select v-model="form.c_id" placeholder="请选择教室">           
          </el-select>
        </el-form-item>
        <el-form-item prop="remarks" label="备 注" >
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="可填写备注说明"
            maxlength="500"
            v-model="form.remarks">
          </el-input>
        </el-form-item>                                                                                                
      </div>                                             
    </el-form>    
  </div>
</template>
<script>
  import axios from 'axios';
  import {addmealcur,addmeals,teacher_cur_n} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import {checkMaxVal100,checkText} from '../../../../../script/validate.js'
  export default {
    name: 'modal',
    props: {
      that: {
        type: Object,
        default: false
      },                  
    },
    data() {
        return {
            status:'',
            tableData: [],
            currmList:[],
            minute:0,
            hour:0,
            today:'',
            c_type:2,//一对多
            form: {
              hours:null,
              minutes:null,
              currm:null,
              student:null,
              c_id:null,              
              timeType:0,
              orgid:this.that.that.userLoginInfo.orgid,
              cur_state:true,
              remarks:'',
              stu_id:null,
              currmName:'',
            },
            rules: {
              orgid: [
              { required: true,},
              ],
              stu_id: [
              { required: true}
              ],
              currm: [
              { required: true}
              ],
              c_id: [
              { required: true}
              ],
              timeType: [
              { required: true}
              ],
              law: [
              { required: true}
              ],                                      
            },
            selectKey: 1,
            Imageport          
        }
    },
    mounted() {
      let tds = this.that.clickId
      if(tds == 0){
        this.today = '日'
      }
      else if(tds == 1){
        this.today = '一'
      }
      else if(tds == 2){
        this.today = '二'
      }      
      else if(tds == 3){
        this.today = '三'
      }      
      else if(tds == 4){
        this.today = '四'
      }      
      else if(tds == 5){
        this.today = '五'
      }      
      else if(tds == 6){
        this.today = '六'
      }      
    },
    created(){
      this.teacher_cur_n ()
    },
    components:{
    	
    },
    methods: {
        hideModal() {
         this.$emit('hideModal');
        },        
        submit() {
          this.addmealcur()
        },
        appendzero:function(obj)
        {
          if(obj<10) return "0" +""+ obj;
          else return obj;
        },              
        async add_teacher_schedule() {
          if(!this.form.stu_id){
            this.$message.error('请选择学生');
            this.$refs.stu_id_Box.$el.children[0].children[0].click();
            return;            
          }
          else if(!this.form.currm){
            this.$message.error('你选择课程');
            this.$refs.currmBox.$el.children[0].children[0].click();
            return;            
          }
          else if(!this.form.law){
            this.$message.error('请选择排课规律');
            this.$refs.lawBox.$el.children[0].children[0].click();
            return;            
          }          
          else if(!this.form.c_id){
            this.$message.error('请选择教室');
            this.$refs.c_id_box.$el.children[0].children[0].click();
            return;            
          };
          let day =  this.that.clickId;
          let toDays = new Date(this.that.toDays + ' ' + this.appendzero(this.form.hours) + ':' + this.appendzero(this.form.minutes) + ':' + '00').getTime()/1000 - new Date(this.that.toDays + ' 00:00:00').getTime()/1000
          let today =  new Date(this.that.toDays + ' 00:00:00').getTime()/1000                                                   
          let resDate = await add_teacher_schedule(this.form.stu_id,this.form.currm,this.that.that.teacherId,this.form.timeType,this.form.law,this.form.c_id,this.form.remarks,day,toDays,today,this.c_type);
          let _this = this;
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });          
            this.that.$emit('submit');
          }
        },
        changeAllClass(){
          console.log(this.$refs.allClaaInput)
          if(this.form.cur_state){
            this.$refs.allClaaInput.$el.style.display="block"
          }
          else{
            this.$refs.allClaaInput.$el.style.display="none"
          }
        },       
        changeValue(event,b){
        	if(event && this.selectKey == '1'){
        		this.selectKey = 2;
        		this.showRight()
        		this.show1 = true;
        	}
          else{
            this.selectKey = 1;
            this.that.submitRight()
            this.show1 = false;            
          }
        },
        changeValue1(event,b){
          if(event && this.selectKey1 == '1'){
            this.selectKey1 = 2;
            this.showRight1()
          }
          else{
            this.selectKey1 = 1;
            this.that.submitRight1()           
          }
        },                
        showRight(){
          //console.log(this.that)
        	this.that.showRight()
        },
        showRight1(){
          this.that.showRight1()
        },
        async teacher_cur_n () {  
          let resDate = await teacher_cur_n(this.that.that.teacherId)
          if (resDate.data.status) {
            this.currmList = resDate.data.data        
          }
        },                      	          
    }
  }
</script>
