<template>
    <div class='lease_model'>
        <el-dialog
            title="用户商品租凭记录"
            :visible.sync="leaseThis.leaseShow"
            v-dialogDrag>
            <section class='table-section div_gulptable'>
                 <el-table
                    :data="tableData.data"
                    stripe
                    style="width: 100%">
                        <el-table-column
                        label="开始时间"
                        min-width='10.29%'
                         align='center'>
                        <template slot-scope="scope">
                          <span>
                            {{scope.row.start_time | timeStampfilters}}
                          </span>
                        </template>
                        </el-table-column>
                        <el-table-column
                        label="结束时间"
                        min-width='10.29%'
                         align='center'>
                         <template slot-scope="scope">
                          <span>
                            {{scope.row.end_time | timeStampfilters}}
                          </span>
                        </template>
                        </el-table-column>
                        <el-table-column
                        label="操作时间"
                        min-width='10.29%'
                         align='center'>
                         <template slot-scope="scope">
                          <span>
                            {{scope.row.update_time | timeStampfilters}}
                          </span>
                        </template>
                        </el-table-column>
                        <el-table-column
                        prop="rent_margin"
                        label="押金"
                        min-width='10.29%'
                         align='center'>
                        </el-table-column>
                        <el-table-column
                        prop="prepay"
                        label="预付金额"
                        min-width='10.29%'
                         align='center'>
                        </el-table-column>
                        <el-table-column
                        prop="rent_amount"
                        label="已收金额"
                        min-width='10.29%'
                         align='center'>
                        </el-table-column>
                        <el-table-column
                        label="支付方式"
                        min-width='10.29%'
                        align='center'>
                        <template slot-scope="scope">
                          <span v-for='item in leaseThis.allPayDate' :key='item.pay_id'>
                            <em v-if='item.pay_id == scope.row.pay_id'>
                              {{item.pay_name}}
                            </em>
                          </span>
                        </template>
                        </el-table-column>
                        <el-table-column
                        prop="manager"
                        label="操作人"
                        min-width='10.29%'
                         align='center'>
                        </el-table-column>
                        <el-table-column
                        prop="remark"
                        label="备注"
                        min-width='13.23%'
                         align='center'>
                        </el-table-column>
                </el-table>
            </section>
            <span slot="footer" class="dialog-footer">
                <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" ></erp-paging>
            </span>
    </el-dialog>
    </div>
</template>
<script>
import erpPaging from '../../../../paging/pagingShort'
import {getrecordsInfo} from '../../../../../getserver/server'
export default {
  name: 'model',
  data () {
    return {
      dialogVisible: true,
      countNum: 0,
      page_num: 1,
      pageSize: 7,
      tableData: []
    }
  },
  inject: ['leaseThis'],
  methods: {
    paging_father (i) {
      this.page_num = i
      this.get_getrecordsInfo()
    },
    get_getrecordsInfo() {
      // 访问列表详情接口
      getrecordsInfo(this.leaseThis.leaseModelDate, this.page_num, this.pageSize).then((res)=>{
        if (res.data.status) {
          this.tableData = res.data.data
          this.countNum = res.data.data.total
        }
      })
    }
  },
  components: {
    erpPaging: erpPaging
  },
  filters: {
    timeStampfilters(val){
      let newDate = new Date(val*1000)
      let yy = newDate.getFullYear();// 年
      let mm = newDate.getMonth() + 1;// 月
      let dd = newDate.getDate();// 日
      if (mm<10){
        mm = '0' + mm
      }
      if (dd < 10) {
        dd = '0' + dd
      }
      let time = yy +'-'+ mm +'-'+ dd
      return time
    }
  },
  watch: {
    'leaseThis.leaseShow'(val){
      if (val==true) {
        // 调用接口
        this.get_getrecordsInfo()
      } else {
        this.leaseThis.leaseModelDate = null
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
.lease_model /deep/ .el-dialog__wrapper .el-dialog{
    @include wh(vw(1360),vw(660));
    display: flex;
    flex-direction: column;
    border-radius: vw(8);
    .el-dialog__header {
        @include wh(100%, vw(60));
        border-top-left-radius: vw(8);
        border-top-right-radius: vw(8);
        @include bg-defaul($bg-color);
        box-sizing: border-box;
        padding: vw(22) vw(30);
        .el-dialog__title{
            color: #fff;
            font-size: vw(16);
            line-height: 0;
        }
        .el-dialog__headerbtn{
            top: vw(20);
            .el-dialog__close{
                color: #fff;
            }
        }
    }
    .el-dialog__body{
        flex: 1;
        overflow: hidden;
    }
    .el-dialog__footer{
        @include wh(100%, vw(80));
        border-top: 1px solid;
        border-color: #e7eaec;
    }
}
</style>