<template>
    <div class='_return'>
        <!-- 归还操作 -->
        <el-dialog
            title="归还商品"
            :visible.sync="leaseThis.returnShow"
            v-dialogDrag>
            <section class='body__section' @scroll="scrollModalMain($event)">
                <div class='body__top'>
                    <ul class='info_list' v-if='leaseThis.returnShopDate!=null'>
                        <li>
                            <span>租客姓名</span>：{{leaseThis.returnShopDate.rent_obj_name}}
                        </li>
                        <li>
                            <span>商品名称</span>：{{leaseThis.returnShopDate.goods_name}}
                        </li>
                        <li>
                            <span>租赁数量</span>：{{leaseThis.returnShopDate.goods_name}}
                        </li>
                        <li>
                            <span>计费方式</span>： <span v-if='leaseThis.returnShopDate.count_type==1'>日</span>
                            <span v-if='leaseThis.returnShopDate.count_type==2'>月</span>
                            <span v-if='leaseThis.returnShopDate.count_type==3'>年</span>
                        </li>
                        <li>
                            <span>租赁押金</span>：{{leaseThis.returnShopDate.rent_margin}}元
                        </li>
                        <li>
                            <span>预付租金</span>：{{leaseThis.returnShopDate.prepay}}元
                        </li>
                        <li>
                            <span>开始时间</span>：{{leaseThis.Timeinfo(leaseThis.returnShopDate.start_time)}}
                        </li>
                        <li>
                            <span>结束时间</span>：{{leaseThis.Timeinfo(leaseThis.returnShopDate.end_time)}}
                        </li>
                        <li>
                           <span>租赁备注</span>：{{leaseThis.returnShopDate.remark}}
                        </li>
                    </ul>
                </div>
                <div class='body__bottom'>
                    <el-form ref="form" :model="form" label-width="20%" :label-position="labelPosition">
                        <el-form-item label="归还时间：" class='xin'>
                            <el-date-picker
                                v-model="form.runTime"
                                type="date"
                                :editable='false'
                                :clearable='false'
                                placeholder="选择日期"
                                :picker-options="pickerOptions">
                            </el-date-picker>
                        </el-form-item>
                        <el-row>
                            <el-col :span='12'>
                                <el-form-item label="实付租金：">
                                        <el-input
                                            type="text"
                                            placeholder="请输入内容"
                                            v-model="form.Actua_mony">
                                        </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span='12'>
                                <el-form-item label="实退金额：">
                                    <el-input
                                        type="text"
                                        disabled
                                        placeholder="请输入内容"
                                        v-model="form.Actuaretreat_mony">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="付款方式：">
                            <el-select v-model="form.Actual_paymentmode" placeholder="请选择">
                                <el-option label= "请选择" value="0"></el-option>
                                <el-option
                                v-for="item in leaseThis.allPayDate"
                                :key="item.pay_id"
                                :label="item.pay_name"
                                :value="item.pay_id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="备 注：">
                            <el-input
                                type="textarea"
                                :rows="3"
                                maxlength='500'
                                placeholder="请输入内容"
                                v-model="form.remark">
                            </el-input>
                        </el-form-item>
                    </el-form>
                </div>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="returnClick" size='small'>确 定</el-button>
                <el-button @click="leaseThis.returnShow= false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getrouterfun} from '../../../../../getserver/server'
export default {
  name: 'return',
  data () {
    return {
      form: {
        runTime: '', // 归还时间
        runTimeStop: '',// 归还时间戳
        Actua_mony: '0',// 实付金额
        Actuaretreat_mony: '',// 实退金额
        Actual_paymentmode: '0',// 付款方式
        remark:''// 备注
      },
      labelPosition: "left",
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() >= Date.now();
        }
      }
    }
  },
  inject: ['leaseThis'],
  created () {
  },
  methods: {
    TimsFrist () {
       let NewDate = new Date()
       let yy = NewDate.getFullYear();// 年
       let MM = NewDate.getMonth() + 1;// 月
       let Da = NewDate.getDate();// 月
       this.form.runTime = yy +'-'+ MM +'-'+ Da
       this.form.runTimeStop = (new Date(this.form.runTime).getTime()) / 1000
    },
    returnClick () {
      // 确定
      if(this.form.runTimeStop == '') {
        this.$message({
            message: '请选择归还时间',
            type: 'error',
            duration: 2000,
        });
        return
      } else if (this.form.Actual_paymentmode == '0') {
        this.$message({
            message: '请选择支付方式!',
            type: 'error',
            duration: 3000,
        });
        return 
      } else if (this.form.Actua_mony =='') {
        this.form.Actua_mony = 0
        this.$message({
            message: '实付金额为空，默认为0处理!',
            type: 'error',
            duration: 3000,
        });
      }
      // 访问接口
      this.get_getrouterfun()
    },
    async get_getrouterfun() {
        let resDate = await getrouterfun(this.leaseThis.returnShopDate.record_id, this.form)
        if (resDate.data.status) {
            this.$message({
                message: '归还成功!',
                type: 'success',
                duration: 3000,
            });
            this.leaseThis.returnShow = false
            this.leaseThis.get_getrecords(this.leaseThis.page_num,this.leaseThis.pageSize)
        }
    }
  },
  watch: {
    'leaseThis.returnShopDate' (val) {
        if (val != null) {
            this.TimsFrist()
            this.form.Actua_mony = this.leaseThis.returnShopDate.rent_amount
            this.form.Actuaretreat_mony = parseFloat(this.leaseThis.returnShopDate.rent_margin) + parseFloat(this.leaseThis.returnShopDate.prepay)
        }
    },
    'leaseThis.returnShow'(val){
      if (val == false) {
        this.leaseThis.returnShopDate = null
        this.form = {
            runTime: '', // 归还时间
            Actua_mony: '0',// 实付金额
            runTimeStop: '',//归还时间戳
            Actuaretreat_mony: '',// 实退金额
            Actual_paymentmode: '0',// 付款方式
            remark:''// 备注
        }
      }
    },
    'form.Actua_mony' (val) {
         // 非零情况 重新计算实退金额
        let actualPrice = val;
        if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                actualPrice = parseFloat(actualPrice);  
            }
            if(actualPrice > 10000000){
                this.$message.error("最大值为100万")
                actualPrice = 10000000
            }  
            this.form.Actua_mony = actualPrice
            if (this.form.Actua_mony==""){
                actualPrice=0
            }                        
        } else {
            this.form.Actuaretreat_mony = (parseFloat(this.leaseThis.returnShopDate.rent_margin) + parseFloat(this.leaseThis.returnShopDate.prepay)) - parseFloat(actualPrice)
        } 
    },
    'form.runTime' (val) {
       if (val != null && val != '') {
          this.form.runTimeStop = (new Date(val).getTime()) / 1000
       }
    }
  }
}
</script>
