<template>
    <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
      <div class="modal-container modal-container1 timeModal">
        <div class="modal-header" @mousedown="setStartingPoint">
          {{ that.title }}
          <i class="el-icon-close" round @click="that.hideCrModal"></i>
        </div>
        <div class="modal-main">
            <section class='div_gulptable'>
              <el-table
                ref="table"
                :data="tableData"
                border
                stripe
                resizable=false
                style="margin-bottom: 1%">
                <el-table-column
                  show-overflow-tooltip
                  align='center'
                  min-width='30.769%'
                  label="上课时间">
                  <template slot-scope="scope"><span>{{scope.row.cur_time | date}}</span></template>
                </el-table-column>
                <el-table-column
                  show-overflow-tooltip
                  align='center'
                  min-width='69.23%'
                  label="调课原因">
                  <template slot-scope="scope">
                    <span>{{scope.row.cur_time | time}}</span>
                  </template>                    
                </el-table-column>                    
              </el-table>
              <erp-paging :counts="counts" :page_num="page_num" :page_size="page_size" @pagings='paging_father' v-if='last_page>1'></erp-paging>
            </section>        
        </div>
        <div class="modal-footer alertFooter">
            <el-button type="primary" round @click="that.submitCr">完成</el-button>
            <el-button round @click="that.hideCrModal">取消</el-button> 
        </div>
      </div>
    </div>
</template> 
<script>
import $ from '../../../../../script/jquery-vendor.js'
import erpPaging from '../../../../paging/pagingShort.vue'
import {schedule} from '../../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
  // ...tableLayer,
  name: 'modal',
  props: {
    that:{
        type: Object,
        default: false        
    },    
  },
  data () {
    return {
      x: 0,
      y: 0,
      node: null,
      isCanMove: false,
      tableData: [],
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数
      page_size:'6'    
    }
  },
  beforeCreate: function () {
   that = this;
  },  
  filters: {//过滤器
　　 date: function (value) {   
     var d = new Date(value * 1000);    //根据时间戳生成的时间对象
     var date = that.appendzero(d.getFullYear()) + "-" + 
     that.appendzero(d.getMonth() + 1) + "-" +
     that.appendzero(d.getDate());           
     return date
   },
   time: function (value) {   
     var d = new Date(value * 1000);    //根据时间戳生成的时间对象
     var date = that.appendzero(d.getHours()) + ":" + 
     that.appendzero(d.getMinutes()) + ":" +
     that.appendzero(d.getSeconds());           
     return date
   },   
　　},
  computed:{
    ...mapState(['userLoginInfo']),
  },
  created: function () {
    this.scheduleList(1)   
  },  
  mounted() {
    this.node = document.querySelector('.timeModal');
  },
  methods: {
    paging_father (n) {
      this.scheduleList(n)
      this.page_num = n;
    },    
    setStartingPoint(e) {
        this.x = e.clientX - this.node.offsetLeft
        this.y = e.clientY - this.node.offsetTop
        this.isCanMove = true
    },       

    modalMove(e) {
        if (this.isCanMove) {
            this.node.style.left = e.clientX - this.x + 'px'
            this.node.style.top = e.clientY - this.y + 'px'
        } 
    },        
    cancelMove() {
        this.isCanMove = false
    },
    changeStartTime(e) {
        this.startTime = e;
        this.scheduleList(1)
    },
    changeEndTime(e) {
        this.endTime = e;
        this.scheduleList(1)
    },              
    appendzero:function(obj)
    {
        if(obj<10) return "0" +""+ obj;
        else return obj;
    },       
    async scheduleList (page) {
        let t = (new Date()).getTime();
        let st
        let et
        if(this.startTime){
            st = this.startTime.getTime()/1000
        }
        else{
            st = this.startTime
        }
        if(this.endTime){
            et = this.endTime.getTime()/1000
        }
        else{
            et = this.endTime
        }        
        let resDate = await schedule(this.that.that.orgid,this.that.id,page)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total          
        }
        console.log(resDate.data.data)
    },       
  },
  components: {
    erpPaging: erpPaging, 
  }
}
</script>
