<template>
    <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1">
            <div class="modal-header" @mousedown="setStartingPoint">
                排课
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <div class="modal-main1">
                <el-form ref="form" :model="form" :rules="rules" label-width="15%" class="fromBox">
                  <div class="rightBox">
                    <el-form-item prop="stu_name" label="选 择 学 生">
                      <el-select v-model="form.student" placeholder="" :disabled="true">
                        <el-option :value="1" :key="1" :label="form.stu_name"></el-option>          
                      </el-select>
                      <span class="stu_span" ref="s1">已购课时：<i>5</i></span><span class="stu_span" ref="s2">未排课时：<i>4</i></span>
                    </el-form-item>
                    <el-form-item prop="class_name" label="课 程 名 称">
                      <el-select v-model="form.class" placeholder="" :disabled="true">
                        <el-option :value="1" :key="1" :label="form.class_name"></el-option>          
                      </el-select>
                    </el-form-item>                                        
                    <el-form-item prop="teacher" label="选 择 教 师" >
                      <el-select v-model="form.teacher" placeholder="请选择教师">           
                      </el-select>
                    </el-form-item>
                    <el-form-item prop="c_num" label="按 规 律 排" >
                      <el-input class="c_num" v-model="form.c_num" placeholder="请填写数值">
                      </el-input>
                      <span class="rightSpan">节课</span>
                    </el-form-item>
                    <el-form-item prop="date" label="日 期">
                      <el-select v-model="form.date" placeholder="" :disabled="true">
                        <el-option :value="1" :key="1" :label="form.dateTime"></el-option>          
                      </el-select>
                    </el-form-item>                    
                    <el-form-item prop="hour" label="上 课 时 间" class="short-item">
                      <el-select v-model="form.timeType" placeholder="请选择" style="margin-right: 5%" :disabled="true">
                        <el-option :value="0" :key="0" label="每周五"></el-option>
                        <el-option :value="1" :key="1" label="每天"></el-option>
                        <el-option :value="2" :key="2" label="每两周五"></el-option>
                      </el-select>          
                      <el-select v-model="form.hour" placeholder="时" class="noRight" :disabled="true">
                        <el-option :value="0" :key="0" label="00"></el-option>
                        <el-option v-for="i in 24" :value="i" :label="appendzero(i)" :key="i"></el-option>
                      </el-select>
                      <el-button class="gh gh1" type="text">时</el-button>
                      <el-select v-model="form.minute" placeholder="分" class="minute noRight" :disabled="true">
                        <el-option :value="0" :key="0" label="00"></el-option>
                        <el-option v-for="i in 60" :value="i" :label="appendzero(i)" :key="i"></el-option>
                      </el-select>
                      <el-button class="gh" type="text">分</el-button>          
                    </el-form-item>
                    <el-form-item prop="charge" label="收 费" >
                      <el-input class="c_num" v-model="form.charge" placeholder="请填写数值">
                      </el-input>
                      <span class="rightSpan">元/课时</span>
                    </el-form-item>                                  
                    <el-form-item prop="price" label="教 室 名 称" >
                      <el-select v-model="form.setMeal" placeholder="请选择教室">           
                      </el-select>
                    </el-form-item>
                    <el-form-item prop="remarks" label="备 注" >
                      <el-input
                        type="textarea"
                        :autosize="{ minRows: 4, maxRows: 10}"
                        placeholder="可填写备注说明"
                        maxlength="500"
                        v-model="form.remarks">
                      </el-input>
                    </el-form-item> 
                    <el-divider></el-divider>
                    <div class="purchasingBox">
                      <el-form-item label="当前课时不足，需要续购课时" class="titleLabel"></el-form-item>
                      <el-form-item prop="charge" label="购 买 课 时">
                        <el-input class="c_num" v-model="form.charge" placeholder="请填写课时">
                        </el-input>
                        <span class="rightSpan">节</span>
                      </el-form-item>
                      <el-form-item prop="validity" label="有 效 期" class="short-item2" style="margin-right: 10%">
                        <el-select v-model="form.validity" placeholder="请选择时间">           
                        </el-select>
                      </el-form-item>
                      <el-form-item prop="purchasing" label="购 课 时 间" class="short-item2">
                        <el-select v-model="form.purchasing" placeholder="请选择时间">           
                        </el-select>
                      </el-form-item>                                                                                                                  
                      <el-form-item prop="pur_remarks" label="购 课 备 注" >
                        <el-input
                          type="textarea"
                          :autosize="{ minRows: 4, maxRows: 10}"
                          placeholder="可填写备注说明"
                          maxlength="500"
                          v-model="form.pur_remarks">
                        </el-input>
                      </el-form-item>
                      <el-form-item prop="amount" label="应 交 金 额" class="short-item2 short-item3" style="margin-right: 10%">
                        <el-input class="amount" v-model="form.amount" placeholder="输入金额">
                        </el-input>                      
                        <span class="rightSpan">元</span>
                      </el-form-item>
                      <el-form-item prop="balance" label="账 户 余 额" class="short-item2 short-item3">
                        <el-input class="balance" v-model="form.balance" placeholder="输入金额">
                        </el-input>                      
                        <span class="rightSpan">元</span>
                      </el-form-item>
                      <el-form-item prop="payType" label="付 款 方 式" >
                        <el-select v-model="form.payType" placeholder="请选择付款方式">
                          <el-option v-for="(item,index) in pay_list" :value="item.pay_id" :key="item.pay_id" :label="item.pay_name"></el-option>                               
                        </el-select>
                      </el-form-item>
                    </div>                                                                                         
                  </div>                                             
                </el-form>                 
              </div>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">保存</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
            <right-modal v-if="show1" ref="rightModal" :that="that1" :orgid="that.that.userLoginInfo.orgid" @hideRightModal="hideRightModal" @submitRight="submitRight" @changeTable="changeTable"></right-modal>
        </div>
    </div>
</template>
<script>
  import {all_pay_list,} from '../../../../../getserver/getAxios.js'
  import {checkText} from '../../../../../script/validate.js' 
  import rightModal from './right-modal/right-continued.vue'
  export default {
    name: 'continued',
    props: {
        that: {
          type: Object,
          default: false
        },                
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            show1:false,
            cheackId:null,
            isCanMove: false,
            minute:0,
            that1:this, 
            pay_list:[],                                   
            form: {
              student:1,
              stu_name:'黄小乐',
              class:1,
              class_name:'钢琴课',
              teacher:null,
              date:1,
              dateTime:'2019-08-26',
              timeType:1,
              hours:null,
              minutes:null,
              remarks: '',
              validity:null,
              purchasing:null,
              pur_remarks:'',
              amount:'',
              balance:'2000',
              payType:null,
            },
            rules: {
                           
            },
            pickerOptions: {
              disabledDate(time) {
                let _now = Date.now(),
                    t = 365 * 24 * 60 * 60 * 1000,
                    //t = new Date(1950, 1, 1).getTime(),
                    sevenDays = _now - t;
                    //console.log(new Date('1970/01/01 00:00:01').getTime())
                    return time.getTime() > _now;
                    //return time.getTime() > _now || time.getTime() < sevenDays;
                },
            },                               
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
        this.showRight()
    },
    created(){
      this.all_pay_list ()
    },
    methods: {
      hideModal() {         
        this.that.hidecomtinuedModal()
      },
      submit() {

      },

      setStartingPoint(e) {
        this.x = e.clientX - this.node.offsetLeft
        this.y = e.clientY - this.node.offsetTop
        this.isCanMove = true
      },

      modalMove(e) {
        if (this.isCanMove) {
          this.node.style.left = e.clientX - this.x + 'px'
          this.node.style.top = e.clientY - this.y + 'px'
        } 
      },
      cancelMove() {
        this.isCanMove = false
      },
      currentSel() {
        let selVal = this.form.id
      },
      appendzero:function(obj)
      {
        if(obj<10) return "0" +""+ obj;
        else return obj;
      },
      hideRightModal() {     
        this.show1 = false;   
      },
      submitRight() {     
        this.show1 = false;
      },
      showRight(){
        this.show1 = true
      },
      changeTable(res){
        this.hideRightModal()
        this.tableData = res;
      },
      changeValue(event,b){
        this.showRight()
        this.show1 = true
      },
      async all_pay_list () {
        let resDate = await all_pay_list(this.that.that.userLoginInfo.orgid)
        if (resDate.data.status) {
          this.pay_list = resDate.data.data
        }
      },                                                                                                
    },
    components: {
      rightModal:rightModal
    }    
  }
</script>
