<template>
	    <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
	        <div class="modal-container modal-container1">
	            <div class="modal-header" @mousedown="setStartingPoint">
	                {{ that.title1 }}
	                <i class="el-icon-close" round @click="hideModal"></i>
	            </div>
	            <div class="modal-main">
                <el-tabs v-model="activeName" @tab-click="handleClick" class="tabBox" ref="tabList">
                  <el-tab-pane label="一对一排课" name="first">
                    <scheduleOne v-if="show" :that="that1" @hideModal="hideModal" @submit="submit" ref="scheduleone"></scheduleOne>
                  </el-tab-pane>
                  <el-tab-pane label="一对多排课" name="second">
                    <scheduleMore v-if="show2" :that="that1" @hideModal="hideModal" @submit="submit" ref="schedulemore"></scheduleMore>
                  </el-tab-pane>
                </el-tabs>                
	            </div>
	            <div class="modal-footer alertFooter">
	                <el-button type="primary" round @click="submit">保存</el-button>
	                <el-button round @click="hideModal">取消</el-button> 
	            </div>
              <right-modal v-if="show1" ref="rightModal" :that="that1" :orgid="that.userLoginInfo.orgid" @hideRightModal="hideRightModal" @submitRight="submitRight" @changeTable="changeTable"></right-modal>
              <rightStudent v-if="show3" ref="rightModal1" :that="that1" :orgid="that.userLoginInfo.orgid" @hideRightModal="hideRightModal" @submitRight="submitRight" @changeTable="changeTable" @hideRightModal1="hideRightModal1" @submitRight1="submitRight1" @changeTable1="changeTable1"></rightStudent>
	        </div>
	    </div>
</template>
<script>
  import rightModal from './right-modal/right-modal.vue'
  import rightStudent from './right-modal/right-student.vue'
  import scheduleOne from './scheduleOne.vue'
  import scheduleMore from './scheduleMore.vue'
  export default {
    name: 'modal',
    props: {
      that: {
        type: Object,
        default: false
      },                  
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            activeName: 'first',
            show:false,
            show1:false,
            show2:false,
            show3:false,
            title1: '选择课程', 
            tableData:[], 
            that1:null, 
            tabIndex:0,
            clickId:this.that.clickId,
            toDays:this.that.dateArry[this.that.clickId-1],//今日是几号 
            stu_id:'',//选择的学生id 
            cur_id:'',//选择的课程id        
        }
    },
    watch: {
      that1:function(){
        this.show = true
      },      
    },
    beforeCreate: function () {
      
    },    
    mounted() {
        this.node = document.querySelector('.modal-container')
        this.that1 = this
    },
    created(){
    },
    components:{
    	scheduleOne:scheduleOne,
      rightModal:rightModal,
      scheduleMore:scheduleMore,
      rightStudent:rightStudent
    },
    methods: {
      setStartingPoint(e) {
        this.x = e.clientX - this.node.offsetLeft
        this.y = e.clientY - this.node.offsetTop
        this.isCanMove = true
      },       

      modalMove(e) {
        if (this.isCanMove) {
          this.node.style.left = e.clientX - this.x + 'px'
          this.node.style.top = e.clientY - this.y + 'px'
        } 
      },        
      cancelMove() {
        this.isCanMove = false
      },      
      transform:function(res){
        if(window.innerWidth <= 1620){
          if(this.activeName == 'first'){
            this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '5%'
          }
          else{
            this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '3%'
          }           
        }         
        else{
          if(this.activeName == 'first'){
            this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '5%'
          }
          else{
            this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '3.5%'
          }             
        }
      },
      handleClick(tab,index){
        //console.log(tab.index)
        this.transform(tab.index)
        this.tabIndex = tab.index
        if(tab.index == 0){
          this.show = true
          this.show2 = false
        }
        else{
          this.show = false
          this.show2 = true
        }
      },            
      hideModal() {
        this.$emit('hideModal');
      },
      getCheckedData() {
        this.$refs.scheduleone.checkedData = this.$refs.rightModal.checkedData
      },           
      submit() {
        if(this.tabIndex == 0){
          this.$refs.scheduleone.add_teacher_schedule()
          // this.$emit('submit');
        }  
      }, 
      hideRightModal() {     
        this.show1 = false;
        if(this.tabIndex == 0){
          this.$refs.scheduleone.selectKey = '1'
        }
        else{
          this.$refs.schedulemore.selectKey = '1'
        }     
      },
      hideRightModal1() {     
        this.show3 = false;
        this.$refs.schedulemore.selectKey1 = '1'      
      },      
      submitRight() {     
        this.show1 = false;
        if(this.tabIndex == 0){
          this.$refs.scheduleone.selectKey = '1'
        }
        else{
          this.$refs.schedulemore.selectKey = '1'
        }
      },
      submitRight1() {      
        this.show3 = false;
        this.$refs.schedulemore.selectKey1 = '1'
      },      
      showRight(){
        this.show1 = true
        this.show3 = false;
        //this.$refs.rightModal.checkedDataArry(this.tableData);
        //this.$refs.rightModal.allCurrm(this.that.userLoginInfo.orgid);
      },
      showRight1(){
        this.show1 = false;
        this.show3 = true
        //this.$refs.rightModal.checkedDataArry(this.tableData);
        //this.$refs.rightModal.allCurrm(this.that.userLoginInfo.orgid);
      },      
      changeTable(res){
          this.hideRightModal()
          this.tableData = res;
          // this.change_cur_value();
          // this.change_actual_price()          
          // console.log(this.tableData)
      },
      changeTable1(res){
          this.hideRightModal1()
          this.tableData = res;
          // this.change_cur_value();
          // this.change_actual_price()          
          // console.log(this.tableData)
      },                                         	          
    }
  }
</script>
<style lang="scss" scoped>
