    font-size:vw(16)!important
  }  		

}
.tabBox /deep/ .el-tabs__content{
  padding:0!important;
  overflow:hidden;
  overflow-y:auto
}
.elTabPane .tabBox /deep/ .el-tabs__active-bar{
  margin: 0;
  margin-left: 5%
}
.tabBox /deep/ .el-tabs__nav-wrap::after{
  height:1px
