	background: #fff;
	border-radius: 10px;
	overflow: visible;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate3d(-50%,-50%,0);
  z-index:1001
}
.modal-container2{
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  position: absolute;
  top: 0%;
  left: 100.5%;
  z-index:11;
  animation:fadeInLeft 0.5s;
  -webkit-animation:fadeInLeft 0.5s; /* Safari and Chrome */  
