<template>
  <div class="menuMadal">
    <div class="menuBox" :style="{left:x1+'px',top:y1+'px'}" @mousemove="modalMove" @mouseup="cancelMove">
      <div class="moveDiv" @mousedown="setStartingPoint"></div>
      <i class="closeMenu delect_icon" @click="closeMenu" @mouseover="overClose" @mouseout="moveClose" ref="closeBtn"></i>
      <div class="studentBox">
        <el-checkbox-group name="checkList" v-model="checkList">
          <el-checkbox label="学生 1  未消课" :value="0" :Key="0" @change="changeStu($event)"></el-checkbox>
          <el-checkbox label="学生 2  未消课" :value="1" :Key="1" @change="changeStu($event)"></el-checkbox>
          <el-checkbox label="学生 3  未消课" :value="2" :Key="2" @change="changeStu($event)"></el-checkbox>
          <el-checkbox label="学生 4  未消课" :value="3" :Key="3" @change="changeStu($event)"></el-checkbox>>          
        </el-checkbox-group>
      </div>
      <div class="btnBox">
        <el-button class="btn1 postpone" @click="showPostpone">从本节课删除</el-button>
        <el-button class="btn1 leave" @click="showLeaveOn">从所有课删除</el-button>
        <el-button class="btn1 add" @click="showLeaveOn">添加排课学生</el-button>
      </div>
      <div class="btnBox">
        <el-button class="btn2 refresh_icon" @click="that.showAlert">集体调课</el-button>
        <el-button class="btn2 refresh_icon" @click="showPostpone">集体顺延</el-button>
        <el-button class="btn2 batch_icon" @click="showCrOn">调课记录</el-button>
        <el-button class="btn2 delect_icon" @click="delectThis">删除本节课</el-button>
        <el-button class="btn2 delect_icon" @click="delectAll">删除所有课</el-button>
        <el-button class="btn2 batch_icon" @click="showtimeTable">分配班级</el-button>
        <el-button class="btn2 batch_icon" @click="showtimeTable">消课详情</el-button>
        <el-button class="btn2 refresh_icon" @click="showComtinuedOn">续排</el-button>
      </div>
      <div class="remarkBox">
        <p><b>系统备注：</b>暂无任何备注</p>
        <div class="remarkUl">
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 4}"
            maxlength="500"
            placeholder="可填写备注说明"
            v-model="textarea1">
          </el-input>
          <el-button>提交备注</el-button>
        </div>
      </div>
    </div>
    <leave v-if="showLeave" :that="that1"></leave>
    <courseRecord v-if="showCr" :that="that1"></courseRecord>
    <continued v-if="showContinued" :that="that1"></continued>
    <deleteModal :show="showDe" :id="id" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    <timeTable v-if="showtb" :title="title3" :orgid="that.orgid" :id="id" @hideModal="hidetimeTable" @submittimeTable="submittimeTable"></timeTable>
  </div>
</template>
<script>
import leave from '../alert-box/leave.vue'
import courseRecord from '../alert-box/courseRecord.vue'
import continued from '../alert-box/continued.vue'
import timeTable from '../../../gulp/teacher-management/alert-box/timeTable.vue'
export default {
  name: 'menu1',
  props: {
    that: {
      type: Object,
      default: false
    },                       
  },
  data () {
    return {
      x1:this.that.menuX,
      y1:this.that.menuY,
      x:0,
      y:0,
      node: null,
      isCanMove: false,      
      textarea1:'',
      id:353,
      showDe:false,
      showContinued:false,
      showCr:false,
      showLeave:false,
      that1:this,
      showtb:false,
      title3:'',
      title:'王二',
      checkList:['学生 2  未消课','学生 1  未消课'],
      checkListId:[0,1],
      delectType:'0',//0是删除，2是顺延,3是取消消课,4是还原,5是离职，6是复职,7是删除本节课，8是删除所有课
    }
  },
  mounted() {
    this.node = document.querySelector('.menuBox')
  },  
  created () {
    
  },
  methods: {
    setStartingPoint(e) {
      this.x = e.clientX - this.node.offsetLeft
      this.y = e.clientY - this.node.offsetTop
      this.isCanMove = true
    },

    modalMove(e) {
      if (this.isCanMove) {
        this.node.style.left = e.clientX - this.x + 'px'
        this.node.style.top = e.clientY - this.y + 'px'
      } 
    },
    cancelMove() {
      this.isCanMove = false
    },
    changeStu(event){
      if(event){
        //this.checkListId.push
      }
      console.log(event)
    },   
    overClose(){
      $(this.$refs.closeBtn).removeClass("delect_icon")
      $(this.$refs.closeBtn).addClass("delete_icon")
    },
    moveClose(){
      $(this.$refs.closeBtn).removeClass("delete_icon")
      $(this.$refs.closeBtn).addClass("delect_icon")
    },
    closeMenu(){
      this.that.hideMenu1()
    },
    showComtinuedOn(a,b){
      this.showContinued = true
    },     
    showLeaveOn(id){
      this.id = id
      this.showLeave = true
    },
    showCrOn(id){
      //this.id = id
      this.showCr = true
    },    
    hideDeleteModal(){
      this.showDe = false
    },
    hidecomtinuedModal(){
      this.showContinued = false
    },        
    hideLeaveModal(){
      this.showLeave = false
    },
    hideCrModal(){
      this.showCr = false
    },    
    submitLeave(id){
      this.showLeave = false
      //this.setLeave(id)
    },
    submitCr(id){
      this.showCr = false
      //this.setLeave(id)
    },    
    submitcomtinued(id){
      this.showContinued = false
      //this.setLeave(id)
    },    
    submitDelete(id){
      if(this.delectType == "2"){
        this.schedulePostpone(id)
      }
      else if(this.delectType == "3"){
        if(this.password == '' || this.password.length < 6){
          this.$message.error('请输入正确的登录密码')
          console.log(this.password)
          return
        }
        this.scheduleRecover(id)
        this.password = ''
      }
      else if(this.delectType == "4"){
        this.reduction(id)
      }        
      else{
        this.delectClasses(id)
      }
      this.showDe = false;
    },    
    showPostpone(id){//顺延
        this.delectType = '2'
        this.deleteId = id
        this.showDe = true
    },
    delectThis(id){//删除本节课
      this.delectType = '7'
      this.deleteId = id
      this.showDe = true
    },
    delectAll(id){//删除所有课
      this.delectType = '8'
      this.deleteId = id
      this.showDe = true
      console.log(this.delectType)
    },        
    showtimeTable(a){
      //this.id = a.id
      //this.title3 = a.name
      this.showtb = true
    },
    hidetimeTable() {
      this.showtb = false
    },
    submittimeTable() {
      this.showtb = false
    },        

  },
  components: {
    leave:leave,
    continued:continued,
    timeTable:timeTable,
    courseRecord:courseRecord,
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin.scss';
.menuMadal{
  
}
.moveDiv{
  width:100%;
  height:20px;
  position: absolute;
  top:0;
  left:0;
  cursor: move;
}
.menuBox:before{
  content: " ";
  width:vw(20);
  height:vw(20);
  border: solid 1px #e7eaec;
  position: absolute;
  left:-9px;
  top:10%;
  border-right: none;
  border-top: none;
  display:block;
  background:#fff;
  transform:rotate(45deg);
}
.menuBox i{
  width:vw(20);
  height:vw(20);
  position: absolute;
  right:2%;
  top:2%;
  display:block;
  cursor: pointer;
}
.menuBox{
  position: fixed;
  z-index:999;
  background-color: #ffffff;
  border-radius: 6px;
  border: solid 1px #e7eaec;
  width:vw(500);
  height:auto;
  padding:vw(5) vw(10);
  box-sizing:border-box;
  font-size:vw(14);
  line-height:vw(24);
  padding:0 vw(20);
  box-shadow: 0px 2px 4px 0px rgba(12, 51, 110, 0.12);  
}
.menuBox .btnBox{
  width:100%;
  padding:vw(20) 0 0;
  border-bottom: 1px solid #e7eaec;
}
.menuBox .btnBox .btn1{
  width:vw(138);
  height:vw(40);
  line-height:vw(40);
  display:inline-block;
  vertical-align:top;
  padding:0;
  margin-right:vw(18);
  margin-bottom:vw(18);
  margin-left:0 
}
.menuBox .btnBox .btn1:last-child{
  margin-right:0
}
.menuBox .btnBox .btn2{
  width:vw(140);
  height:vw(40);
  border-radius: 6px;
  border: solid 1px #e7eaec;
  margin-right:vw(18);
  margin-bottom:vw(18);
  margin-left:0;
  box-sizing:border-box;
  padding:0;
  background-size:18% auto;
  background-position: 10% center;
  text-align:left;
  text-indent: 30%
}
.menuBox .btnBox .btn2:nth-child(3n){
  margin-right:0
}
.remarkBox p{
  margin-top:vw(16);
  margin-bottom:vw(16)
}
.remarkBox .remarkUl{
  width:100%;
  overflow:auto;
  margin-bottom:vw(20)
}
.remarkBox .remarkUl .el-textarea{
  width:vw(300);
  height:vw(110);
  float:left;
  border-radius: 6px;
  border: solid 1px #e7eaec;
  overflow: hidden; 
}
.remarkBox .remarkUl /deep/ .el-textarea textarea{
  border: none;
  box-sizing:border-box;
}
.remarkBox .remarkUl button{
  width: vw(120);
  height: vw(40);
  line-height:vw(40);
  margin-left:vw(20);
  background-color: #2769fb;
  border-radius: 6px;
  color:#fff;
  padding:0;
  float:left
}
.leave{
  border: solid 1px #ff983d;
  color:#ff983d
}
.add{
  border: solid 1px #09e197;
  color:#09e197
}
.postpone{
  border: solid 1px #47b6ff;
  color:#47b6ff
}
.studentBox{
  width:100%;
  height: vw(100);
  overflow:hidden;
  overflow-y:auto;
  margin-top:2%;
  border-bottom:1px solid #ededed;
}
.studentBox label{
  width:50%;
  float: left;
  margin:2% 0;
}
.studentBox /deep/ .el-checkbox__label{
  color:#606266!important;
}
.studentBox /deep/ .el-checkbox__inner::after{
  border: 1px solid #999;
  border-left: 0;
    border-top: 0;
}
.studentBox /deep/ .el-checkbox__input.is-checked .el-checkbox__inner,.studentBox /deep/ .el-checkbox__input.is-focus .el-checkbox__inner,.studentBox /deep/ .el-checkbox__inner:hover{
  background-color: #fff;
    border-color: #999;
}
@media screen and(max-width: 1381px){
  .menuBox{
    width:vw1(500);
    padding:0 vw1(20);
  }
  .menuBox .btnBox{
    width:100%;
    padding:vw1(20) 0 0;
  }
  .menuBox .btnBox .btn1{
    width:vw1(138);
    height:vw1(40);
    line-height:vw1(40);
    margin-right:vw1(18);
    margin-bottom:vw1(18);    
  }
  .menuBox .btnBox .btn2{
    width:vw1(140);
    height:vw1(40);
    margin-right:vw1(18);
    margin-bottom:vw1(18);
  }
  .remarkBox p{
    margin-top:vw1(16);
    margin-bottom:vw1(16)
  }
  .remarkBox .remarkUl .el-textarea{
    width:vw1(300);
    height:vw1(110);
  }
  .remarkBox .remarkUl{
    margin-bottom:vw1(20)
  }
  .remarkBox .remarkUl button{
    width: vw1(120);
    height: vw1(40);
    line-height:vw1(40);
    margin-left:vw1(20);
  }
  .menuBox:before{
    width:vw1(20);
    height:vw1(20);
  }
  .menuBox i{
    width:vw1(20);
    height:vw1(20);
  }
  .studentBox{
    height: vw1(100);
  }
  
}
@media screen and(min-width: 1381px){
 
}
</style>
