<template>
    <div class='_top-up'>
        <!-- 充值 - 弹窗 -->
        <el-dialog
            title="充值"
            v-dialogDrag
            :visible.sync="tablesThis.TopupShow">
            <span class='span_body'>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="17%" class="demo-ruleForm">
                    <el-form-item label="当前余额" prop="name">
                        :<el-input v-model="ruleForm.balance" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="充值金额" prop="name">
                        :<el-input v-model="ruleForm.recharge" placeholder='请输入充值金额'></el-input>
                    </el-form-item>
                    <el-form-item label="赠送金额" prop="name">
                        :<el-input v-model="ruleForm.give" placeholder='请输入赠送金额'></el-input>
                    </el-form-item>
                    <el-form-item label="充值时间" prop="name">
                        :<el-date-picker
                            v-model="ruleForm.time"
                            type="date"
                            :editable='false'
                            prefix-icon="el-icon-caret-bottom"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="付款方式" prop="name">
                        :<el-select v-model="ruleForm.payActiveid" placeholder="请选择方式">
                            <el-option
                            v-for="item in ruleForm.pay"
                            :key="item.pay_id"
                            :label="item.pay_name"
                            :value="item.pay_id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="备注" prop="name">
                        :<el-input type="textarea" v-model="ruleForm.remark" placeholder='可填写备注说明'></el-input>
                    </el-form-item>
                </el-form>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="topUpClick" size='small'>确 定</el-button>
                <el-button @click="tablesThis.TopupShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getTopUp} from '../../../../../getserver/server'
export default {
  name: 'top_up',
  data () {
    return {
      dialogVisible: true,
      ruleForm: {
        stu_id: '',// 学生id
        balance: 0, // 余额
        recharge: '', // 充值金额
        give: '', // 赠送金额
        time: '',// 充值时间
        payActiveid: '',// 支付方式选中
        pay: [], // 支付方式
        remark: '' // 备注
      }
    }
  },
  inject: ['tablesThis'],
  created(){
    
  },
  methods: {
    topUpClick(){
      this.get_getTopUp()
    },
    async get_getTopUp(){
       let resDate = await getTopUp(this.ruleForm)
       if (resDate.data.status) {
           // 充值成功
            this.$message({
              message: "充值成功",
              type: 'success',
              duration: 2000,
            })
            this.tablesThis.getStudentlist()
       }
    }
  },
  watch: {
    'tablesThis.TopupData' (val) {
        if (val!=null) {
          const _this = this
          this.ruleForm.balance = val.balance; // 余额
          this.ruleForm.stu_id = val.stu_id;// 学生id
          this.ruleForm.remark = val.remarks;// 备注
          this.ruleForm.time = this.tablesThis.getThisTime();
          this.tablesThis.get_all_pay_list((row)=>{
            _this.ruleForm.pay = row
          });
        }
    },
    'tablesThis.TopupShow' (val) {
        if (val == false) {
            this.tablesThis.TopupData = null
            this.ruleForm = {
                balance: 0, // 余额
                topupAmount: 0, // 充值金额
                givingAmount: 0, // 赠送金额
                time: '',// 充值时间
                pay: [], // 支付方式
                note: '' // 备注
            }
        }
    },
    'ruleForm.recharge'(val) {
      if (val!="") {
          let actualPrice = val;                    
            if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
                if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                    actualPrice= parseFloat(actualPrice);  
                }
                if(actualPrice > 10000000){
                    this.$message.error("最大值为100万")
                    actualPrice = 10000000
                }                                             
            }
            this.ruleForm.recharge = actualPrice
      }
    },
    'ruleForm.give'(val) {
      if (val!="") {
          let actualPrice = val;                    
            if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
                if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                    actualPrice= parseFloat(actualPrice);  
                }
                if(actualPrice > 10000000){
                    this.$message.error("最大值为100万")
                    actualPrice = 10000000
                }                                             
            }
            this.ruleForm.give = actualPrice
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
@media screen and (max-width: 1380px){
  ._top-up {
