<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="4" style="display: flex;">
          <el-button class="el-icon-arrow-left dateBtn prveBtn"></el-button>
          <span class="dateSpan">
            <el-date-picker
              v-model="date"
              prefix-icon="el-icon-caret-bottom"
              type="week"
              format="yyyy 第WW周"
              :clearable="false"
              placeholder="选择周"
              @change="changeDate">
            </el-date-picker>
          </span>
          <el-button class="el-icon-arrow-right dateBtn nextBtn"></el-button>          
        </el-col>
        <el-col :span="20">
            <div class="grid-content bg-purple-dark _tabTopconent rightBox">                                      	          	
            	<label class='label_elect selectTwo' style="margin-left: 0">
                <span>科目： </span>
                <el-select v-model="courseId1" placeholder="全部" ref="selectRef1" @change="changeKc($event)">
                  <el-option :value="null" label="全部"></el-option>
                  <el-option v-for="(item,index) in course" :value="item.sid" :key="item.sid" :data-index='index' :label="item.sname"></el-option>
                </el-select>
              </label>
              <label class='label_elect selectTwo' style="margin-left: 0;margin-right: 8%">
                <span>课程： </span>
                <el-select v-model="courseId2" placeholder="全部" ref="selectRef2" @change="changeKc1($event)">
                  <el-option :value="null" label="全部"></el-option>
                  <el-option v-for="(item,index) in courseList" :value="item.cur_id" :key="item.cur_id" :label="item.cur_name"></el-option>
                </el-select>
              </label>
              <label class='label_elect selectTwo' style="margin-left: 0">
                <span>教师： </span>
                <el-select v-model="teacher" placeholder="===" popper-class="treeSelect" @change="changeTeacher($event)">
                  <el-option v-for="(item,index) in teacherList" :value="item.t_id" :key="item.t_id" :label="item.t_name"></el-option>
                </el-select>
              </label>
              <label class='label_elect selectTwo longSelect' style="margin-left: 0">
                <span>手机号： </span>
                <el-input v-model.trim="phone" placeholder="" maxlength="11" :disabled="true"></el-input>
              </label>                            
              <label class='label_elect selectTwo longSelect' style="margin-left: 0;margin-right: 0">
                <span>选择显示： </span>
                <el-select v-model="value" placeholder="选择" popper-class="treeSelect" @change="changeType($event)">
                  <el-option value="0" key="0" label="学生"></el-option>
                  <el-option value="1" key="1" label="课程"></el-option>
                </el-select>
              </label>               
            </div>
        </el-col>
      </el-row>
      <dialogComponent v-if="show" :show="show" :that="that" :title="title" @hideModal="hideModal" @submit="submit" :orgid="userLoginInfo.orgid">
      </dialogComponent>     
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import dialogComponent from '../alert-box/alertBox'
import Imageport from '../../../../../config/ImagePz.js'
import {get_teacher_by_course,all_list_currm,get_tea_schedules} from '../../../../../getserver/getAxios' 
import {mapState} from 'vuex'
export default {
    name: 'tabTops',
    props: {
      that: {
        type: Object,
        default: false
      },      
    },     
    data () {
    	return {
        value: '0',
        title: '新增分类',
        show: false,
        inputVal:'',
        phone:18800000000,
        date:new Date(),
        teacherList:[],//教师列表
        teacher:null,
        course:[],//科目列表
        courseList:[],//课程列表
        studentsList:[],//学生列表
        courseId1:null,
        courseId2:null,
        start_time:'',
        end_time:'',
        storeList:[
        {"id":1,"name":"11"}
        ],
        Imageport    
      }
    },
    mounted() {
      let min = this.date.getDay()
      let max = 7-this.date.getDay()
      // for(let i=this.date.getDay();i>0;i--){
      //   min--
      // }
      for(let i=this.date.getDay();i<7;i++){
        max++
      }
      this.start_time = new Date(this.date.getFullYear()+'-'+this.appendzero((this.date.getMonth()+1))+'-'+this.appendzero(this.date.getDate()-min) +' 00:00:00').getTime()/1000
      this.end_time = new Date(this.date.getFullYear()+'-'+this.appendzero((this.date.getMonth()+1))+'-'+this.appendzero(this.date.getDate()+max-this.date.getDay()) +' 00:00:00').getTime()/1000
      //console.log(this.start_time,this.end_time)      
    },
    created(){
      this.$parent.nowWeek = this.date
      this.teacher_list()
      this.all_list_currm()
    },
    computed:{
      ...mapState(['userLoginInfo']),
    },    
    methods: {
      appendzero:function(obj)
      {
        if(obj<10) return "0" +""+ obj;
        else return obj;
      },            
      changeDate(){
        this.$parent.nowWeek = this.date
        let min = this.date.getDay()+1,max = this.date.getDay()-1
        for(let i=this.date.getDay();i>0;i--){
          min--
        }
        for(let i=this.date.getDay();i<7;i++){
          max++
        }
        this.start_time = new Date(this.date.getFullYear()+'-'+this.appendzero((this.date.getMonth()+1))+'-'+this.appendzero(this.date.getDate()-min) +' 00:00:00').getTime()/1000
        this.end_time = new Date(this.date.getFullYear()+'-'+this.appendzero((this.date.getMonth()+1))+'-'+this.appendzero(this.date.getDate()+max-this.date.getDay()) +' 00:00:00').getTime()/1000   
      },       
      changeKc(e){
        let obj = {}
        obj = this.course.find((item)=>{
          return item.sid === e
        })
        //console.log(obj)       
        if(obj != undefined){
          this.courseList = obj.courses
          this.courseId2 = null           
        }
        else{
          this.courseId1 = null
          this.courseId2 = null
        }
        this.teacher_list()
      },
      changeKc1(e){
        this.teacher_list()
      },
      changeTeacher(e){
        this.that.teacherId = this.teacher
        this.that.title1 = this.teacherList[e].t_name
        this.phone = this.teacherList[e].cellphone
        this.get_tea_schedules ()
      },
      changeType(event){
        console.log(event)
        this.that.topType = event
      },     
      async teacher_list () {
        let resDate = await get_teacher_by_course(this.courseId1,this.courseId2)
        if (resDate.data.status) {
         this.teacherList =resDate.data.data
         if(this.teacherList.length != 0){
           this.teacher = this.teacherList[0].t_id
           this.phone = this.teacherList[0].cellphone
           this.that.teacherId = this.teacher
           this.that.title1 = this.teacherList[0].t_name
           this.get_tea_schedules()             
         }
         else{
          this.teacher = null
         }       
       }
      },
      async all_list_currm () {
        let resDate = await all_list_currm(this.that.userLoginInfo.orgid)
        if (resDate.data.status) {
         this.course =resDate.data.data        
       }     
     },
     async get_tea_schedules () {
      let resDate = await get_tea_schedules(this.teacher,this.start_time,this.end_time,this.courseId1,this.courseId2)
      if (resDate.data.status) {
        this.that.tableData =resDate.data.data        
      }                       
     },
    },
    components: {
      Lbutton: Lbutton,
      dialogComponent:dialogComponent,
    },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
  }
  .el-col .rightBox{
  	text-align:right;
  	display:flex;
    justify-content : flex-end;
    //justify-content: space-between;
  }
  .el-col .rightBox .hot_btn{
  	float: right;
  	margin-left:vw(20);
  	margin-right:0
  }
  .label_search{
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid #e7eaec;
    padding-left: vw(12);
    padding-right: vw(12);
    box-sizing: border-box;
    border-radius: 6px;
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(20.52%, vw(40));
  }
  .selectTwo{
    @include wh(vw(184), vw(40));
    margin-right:vw(20)
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        height: 100%;
        /deep/ .el-input {
          height: 100%;
          .el-input__inner{
            height: 100%;
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .hot_btn{
    @include wh(auto, vw(40));
    margin-right: vw(20);
    padding:  0 0.5vw;
    .icon-width{
        margin-right: vw(10)
    }
  }
  .selectone1 /deep/ .el-input__inner{
    font-size:vw(16);
    padding:0 vw(10)
  }
  .longSelect{
    width:vw(220);
    margin-right:15%

  }
  .repair_button{
    margin-right:vw(80)
  }
  .dateSpan{
    width:vw(150);
    height:vw(40)
  }
  .dateSpan /deep/ .el-date-editor.el-input{
    width:100%;
  }
  .dateSpan /deep/ .el-icon-caret-bottom:before{
    display:none
  }
  .dateSpan /deep/ input{
    text-align:center;
    height:vw(40)
  }
  .dateBtn{
    width:vw(40);
    height:vw(40);
    border-radius: 6px;
    border: solid 1px #e7eaec;
    padding:0;
    display:inline-block
  }
  .nextBtn{
    margin-left:vw(10);
  } 
  .prveBtn{
    margin-right:vw(10)
  }
  .meadio .el-col .grid-content .label_elect{
    white-space: nowrap;
  }
  .longSelect /deep/ .el-input__inner {
    line-height:vw(40);
    height:vw(40)
  }
  .dateSpan /deep/ .el-input__inner{
    width:100%;
    padding:0
  }
  .store /deep/ .el-input__inner{
    width:vw(120)
  }
}
@media screen and (max-width: 1440px) {
  // 当文档小于1440时起效
  .meadio {
  	display: block;
  	-webkit-box-orient: vertical;
  	-webkit-box-direction: normal;
  	-ms-flex-direction: column;
  	flex-direction: column;
    .el-col-12{
      
    }
    .selectone1{
    	//width:40%;
    }
    .el-col-12:last-child{
      margin-top: 0px;
    }
  }
  .selectTwo /deep/ .el-input__inner{
    font-size:vw1(16);
    padding:0 vw1(10)
  }
  ._tabTop{
    // margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
    .label_elect{
      font-size:16px
    }
    .selectTwo{
      @include wh(vw1(150), vw1(40));
      width:vw1(150)!important;
      margin-right:vw1(20)
    }
    .selectTwo.longSelect{
      width:vw1(190)!important;
      margin-right:15%
    }
    .longSelect /deep/ .el-input__inner {
      line-height:vw1(40);
      height:vw1(40)
    }
    .dateSpan /deep/ .el-input__inner{
      width:100%
    }
    .store /deep/ .el-input__inner{
      width:vw1(120)
    }                   
  }
  .selectone{
    @include wh(20.52%, vw1(40));
  }
  .selectTwo{
    @include wh(vw1(220), vw1(40));
  }
  .repair_button{
    margin-right:vw1(80)
  }
  .dateSpan /deep/ input{
    text-align:center;
    height:vw1(40)
  }  
  .dateSpan{
    width:vw1(120);
    height:vw1(40)
  }
  .dateBtn{
    width:vw1(40);
    height:vw1(40);
    border-radius: 6px;
    border: solid 1px #e7eaec;
    padding:0;
    display:inline-block
  }
  .nextBtn{
    margin-left:vw1(10);
  } 
  .prveBtn{
    margin-right:vw1(10)
  }         
}
</style>
