<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './td-schedule-top/td-schedule-top'
import remarks from '../../../public/remarks.vue'
import tipBox from './alert-box/tipBox.vue'
import menuBox from './alert-box/menu.vue'
import menuBoxMore from './alert-box/menuMore.vue'
import scheduleClass from './alert-box/scheduleClass.vue'
import editTdSchedule from '../td-schedule/alert-box/alertBox.vue'
import {cate_del} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
// tableData: [
//       {tb:[{student:'林乐曦',td:1,time:1568260121,endTime:1568262641,surplus:1},{student:'黄小乐',td:2,time:1568269215,endTime:1568273415,surplus:2,truancy:1}]},
//       {tb:[{student:'林乐曦',td:3,time:1568505600,endTime:1568508600,surplus:5},{student:'周小小',td:4,time:1568529000,endTime:1568532600,surplus:1,schedule:1}]},
//       {tb:[]},
//       {tb:[{student:'周小小',td:5,time:1568514600,endTime:1568516400,surplus:1,adjustment:1}]},
//       {tb:[]},
//       {tb:[]},
//       {tb:[{student:'林乐曦',td:6,time:1568541600,endTime:1568546400,surplus:1,deletedClass:1}]},
//       ],
let that
export default {
	// ...tableLayer,
  name: 'classification',
  data () {
    return {
      x: 0,
      y: 0,
      node: null,
      isCanMove: false,      
      that: this,
      tableData: [
      {tb:[]},
      {tb:[]},
      {tb:[]},
      {tb:[]},
      {tb:[]},
      {tb:[]},
      {tb:[]},
      ],
		  value: '0',
		  title: '钢琴课',
      title1: '',
		  show: false,
      show1: false,
		  showDe:false,
		  deleteId:null,
      toDay: new Date(new Date(new Date().toLocaleDateString()).getTime() + 7 * 60 * 60 * 1000 ).getTime()/1000,//7点的时间戳
      nowDate:null,
      tipPosition:false,
      menuBoxPosition:false,
      menuBoxPosition1:false,
      tipX:0,
      tipY:0,
      tipData:{},
      menuX:0,
      menuY:0,
      nowWeek:null,
      dateArry:[],
      clickId:null,//当前点击的哪一行index
      teacherId:null,//当前老师id
      topType:0,//头部选择学生显示类型

    }
  },
  watch: {
    nowDate:function(){
      var t1 =  window.setTimeout(function(){
        that.nowDate = new Date()
        //console.log(Date.parse(that.nowDate),that.toDay)
        window.clearTimeout(t1);//去掉定时器 
      },120000)
    }, 
    nowWeek:function(){
      let val = this.nowWeek
      this.dateArry=[]
      for(let i=val.getDay();i>0;i--){
        this.dateArry.push(val.getFullYear()+'-'+that.appendzero((val.getMonth()+1))+'-'+that.appendzero(val.getDate()-i))
      }
      for(let i=val.getDay();i<7;i++){
        this.dateArry.push(val.getFullYear()+'-'+that.appendzero((val.getMonth()+1))+'-'+that.appendzero(val.getDate()+i-val.getDay()))
      }      
    } 
  },
  beforeCreate: function () {
  	that = this;
  },  
  computed:{
  	...mapState(['userLoginInfo']),
  },
  filters:{
    serialNumber (val){
      let n
      if(val == 0){
        n = '一'
      }
      else if(val == 1){
        n = '二'
      }
      else if(val == 2){
        n = '三'
      }      
      else if(val == 3){
        n = '四'
      }      
      else if(val == 4){
        n = '五'
      }      
      else if(val == 5){
        n = '六'
      }      
      else if(val == 6){
        n = '日'
      }           
      return '星期'+n
    }, 
  }, 
  methods: {
    appendzero:function(obj)
    {
      if(obj<10) return "0" +""+ obj;
      else return obj;
    },    
    dragStart(event) {
      console.log(11)
      //console.log(event)
      this.hideTip ()
      event.dataTransfer.setData("Text", event.target.id);
    },    
    dragging(event) {
      //document.getElementById("demo").innerHTML = " p 元素正在拖动";
    },
    allowDrop(event) {
      event.preventDefault();
    },
    drop(event) {
      //console.log(event)
      this.hideMenu()
      event.preventDefault();
      var data = event.dataTransfer.getData("Text");
      event.target.appendChild(document.getElementById(data));
      document.getElementById(data).style.left = (event.clientX/document.body.clientWidth*100 - 3.125 - event.path[3].children[0].clientWidth/document.body.clientWidth*100)*document.body.clientWidth/100-parseFloat(document.getElementById(data).style.width)*event.path[2].clientWidth/100/2 + 'px'
      this.showAlert()
      //document.getElementById("demo").innerHTML = "p 元素已被拖动";
    },    
    headClass ({row, rowIndex}){
      if (rowIndex === 1) {
        return 'warning-row';
      }
      if (rowIndex === 0) {
        return 'warning-row1';
      }      
      return '';
    },
    tdClass({row, column, rowIndex, columnIndex}){
      if (columnIndex === 0) {
        return 'dateCell';
      }
      return '';    
    },
    showTip (e,b){
      if(this.isCanMove == false){
        this.tipX = e.x
        this.tipY = e.y
        this.tipPosition = true
        this.tipData = b    
      }
      // console.log($(e))
    },
    hideTip (e){
      this.tipPosition = false
    },
    showMenu (e){
      e.stopPropagation();
      if(this.isCanMove == false){
        this.menuX = $(e.path[0]).offset().left -10
        this.menuY = $(e.path[0]).offset().top+e.path[0].clientHeight+20
        this.menuBoxPosition = true        
      }
      // console.log($(e))
    },
    showMenu1 (e){
      e.stopPropagation();
      if(this.isCanMove == false){
        this.menuX = $(e.path[0]).offset().left + e.path[0].clientWidth + 20
        this.menuY = $(e.path[0]).offset().top - e.path[0].clientHeight
        this.menuBoxPosition1 = true        
      }
    },    
    hideMenu (e){
      this.menuBoxPosition = false
    },
    hideMenu1 (e){
      this.menuBoxPosition1 = false
    },                                
    showAlert(a,b){
    	this.show = true
    },
    showAlert1(a){
      this.clickId = a+1
      this.show1 = true
    },       
    hideModal() {
    // 取消弹窗回	        
	    this.show = false
    },
    hideModal1() {
    // 取消弹窗回          
      this.show1 = false
    },    
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	this.showDe = false;
    	this.cate_del(id)
    },
    submit() {
    // 确认弹窗回调   
	    this.last_page = 0
        this.paging_father(this.page_num)
	    this.show = false
    },
    submit1() {
    // 确认弹窗回调   
      this.show1 = false
      this.$refs.thisTop.get_tea_schedules()
    },    
    arraySpanMethod ({ row, column, rowIndex, columnIndex }) {
      // console.log(row, column, rowIndex, columnIndex)
      // 打印出的数据就是表格当前行的数据,当前列的数据,索引
      for(var i=0;i<this.tableData.length;i++){
        if (rowIndex === i) {
          // 合并第二行
          if (columnIndex === 1) {
            // 从第二列开始
            return [1, 17]  
          }
        }        
      }
    }   
  }, 
  created: function () {
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
  	$(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px") 
    this.nowDate = new Date()	
  },
  components: {
  	erpTabtop: erpTabtop,
  	remarks: remarks,
  	editTdSchedule: editTdSchedule,
    tipBox:tipBox,
    menuBox:menuBox,
    scheduleClass:scheduleClass,
    menuBoxMore:menuBoxMore
  }
}
</script>
